import { graphql, useStaticQuery } from "gatsby";
import { GatsbyImage } from "gatsby-plugin-image";
import * as React from "react";

const CustomerBase: React.FC = () => {
  const data = useStaticQuery(graphql`
    query {
      allFile(
        sort: { name: ASC }
        filter: {
          relativePath: { regex: "/companies/*/" }
          sourceInstanceName: { eq: "images" }
        }
      ) {
        nodes {
          id
          name
          childImageSharp {
            gatsbyImageData
          }
        }
      }
    }
  `);

  return (
    <div className="p-3 select-none">
      <p className="text-center pt-5">
        We are blessed to work with leading brands!
      </p>
      <div className="relative flex overflow-x-hidden">
        <div className="py-12 animate-marquee whitespace-nowrap flex items-center">
          {data.allFile.nodes.map((image: any) => {
            return (
              <GatsbyImage
                key={image.name}
                className="w-28 mx-10"
                image={image.childImageSharp.gatsbyImageData}
                alt={image.name}
              />
            );
          })}
        </div>

        <div className="absolute top-0 py-12 animate-marquee2 whitespace-nowrap flex items-center">
          {data.allFile.nodes.map((image: any) => {
            return (
              <GatsbyImage
                key={image.name}
                className="w-28 mx-10"
                image={image.childImageSharp.gatsbyImageData}
                alt={image.name}
              />
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default CustomerBase;
